import React, {Component} from 'react';
import isValidURL from '../../helpers/validators';

import AddUrlForm from './addUrlForm';
import {API_URL} from '../../helpers/constants';

class AddUrlContainer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            valid: true,
            errorMessage: '',
            originalLink: ''
        };

        this.validate = this.validate.bind(this);
        this.handleError = this.handleError.bind(this);
    }

    validate(value) {
        const message = value ? 'This is not a valid URL' : 'Field can not be empty';
        const isValid = isValidURL(value);
        this.setState({
            ...this.state,
            valid: isValid,
            errorMessage: message
        });

        isValid && this.handleSubmit(value);
    }

    handleSubmit(value) {
        const {hwid, addUrl} = this.props;
        const payload = {
            originalLink: value,
            extraVars: {
                hwid: hwid,
                generateInviteCode: true
            }
        };
        console.log('Я ЕСТЬ ПЕЙДОАД' + JSON.stringify(payload));

        fetch(API_URL, {
            method: "GET",
            //body: JSON.stringify(payload)
        })
            .then((response) => {
                if (response.status !== 200) {
                    this.handleError(response.statusText);
                    return;
                }
                response.json().then((data) => addUrl({...data, originalLink: value}));
            })
            .catch(error => this.handleError(error))
    }

    handleError(errorText) {
        this.setState({...this.state, valid: false, errorMessage: errorText});
    }

    render() {
        const {valid} = this.state;
        return(
            <div>
                <AddUrlForm validate={this.validate}/>
                {!valid && this.renderError()}
            </div>
        )
    }

    renderError() {
        const {errorMessage} = this.state;

        return(
            <span>{errorMessage}</span>
        )
    }
}

export default AddUrlContainer;