import {Component} from 'react';

class PWController extends Component {
    constructor(props) {
        super(props);

        this.getHWID = this.getHWID.bind(this);
        this.getSubscriptionStatus = this.getSubscriptionStatus.bind(this);
    }

    componentDidMount() {
        window.Pushwoosh.push(this.getHWID);
        window.Pushwoosh.push(this.getSubscriptionStatus);
        window.Pushwoosh.push(['onSubscribe', this.getSubscriptionStatus]);
        window.Pushwoosh.push(['onUnsubscribe', this.getSubscriptionStatus]);
    }

    getHWID() {
        window.Pushwoosh.getHWID().then((hwid) => this.props.storeHWID(hwid));
    }

    getSubscriptionStatus() {
        window.Pushwoosh.isSubscribed().then((status) => this.props.storeSubscriptionStatus(status));
    }

    render() {
        return(
            null
        )
    }
}

export default PWController;