import React, {Component} from 'react';

import PWController from '../components/pushwoosh';
import AddUrlContainer from './addUrl/addUrlContainer';
import UrlCart from '../components/urlCart';
import SpecialOffer from '../components/specialOffer';

class Applicaton extends Component {
    constructor(props) {
        super(props);
        this.state = {
            shortedLinks: [],
            hwid: '',
            isSubscribed: null
        };

        this.storeHWID = this.storeHWID.bind(this);
        this.storeSubscriptionStatus = this.storeSubscriptionStatus.bind(this);
        this.addUrl = this.addUrl.bind(this);
    }

    storeHWID(hwid) {
        this.setState({...this.state, hwid});
    }

    storeSubscriptionStatus(status) {
        this.setState({...this.state, isSubscribed: status});
    }

    addUrl(urlData) {
        const customUrlData = {
            id: 'abcd',
            shortLink: 'krtsh.co',
            originalLink: urlData.originalLink,
            expireDate: 1556320622,
            extraVars: {
                inviteCode: 'invite-code'
            }
        };

        this.setState({...this.state, shortedLinks: [{...customUrlData}]})
    }

    render() {
        const {hwid, shortedLinks, isSubscribed} = this.state;
        console.log(this.state);
        return(
            <div>
                <PWController storeHWID={this.storeHWID} storeSubscriptionStatus={this.storeSubscriptionStatus}/>
                <AddUrlContainer addUrl={this.addUrl} hwid={hwid}/>
                {shortedLinks.map(linkData => <UrlCart key={linkData.id} linkData={linkData}/>)}
                {shortedLinks.length > 0 && <SpecialOffer isSubscribed={isSubscribed}/>}
            </div>
        )
    }
}

export default Applicaton;