import React, {Component} from 'react';
import styles from "../shortener.module.css";

class AddUrlForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: ''
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({value: event.target.value});
    }

    handleSubmit(event) {
        event.preventDefault();
        const {value} = this.state;

        this.props.validate(value);
    }

    render() {
        const {value} = this.state;

        return(
            <div>
                <form onSubmit={this.handleSubmit}>
                    <input placeholder="Place a link to shorten it" className={styles.input} type="text" value={value}
                           onChange={this.handleChange}/>
                    <div className={styles.buttonGroup}>
                        <input className={styles.cta} type="submit" value="Shorten"/>
                    </div>
                </form>
            </div>
        )
    }
}

export default AddUrlForm;